
import EncounterMixinVue from '@/views/EncounterMixin.vue'
import { FieldType } from "@/components/Forms/BaseFormElements"
import { defineComponent } from 'vue'
import { Field, Option } from '@/components/Forms/FieldInterface'
import { IonPage } from "@ionic/vue"
import { HtsPartnerReceptionService } from "@/apps/HTS/services/hts_partner_reception_service"

export default defineComponent({
  components: { IonPage },
  mixins: [EncounterMixinVue],
  data: () => ({
    service: {} as any,
    fieldComponent: ''
  }),
  watch: {
    ready: {
      handler(ready: boolean) {
        if (ready) {
          this.service = new HtsPartnerReceptionService(this.patientID, this.providerID)
          this.fields.push(this.partnerPresentField())
          this.fields.push(this.partnerHivStatusField())
        }
      },
      immediate: true
    }
  },
  methods: {
    onFinish(formData: any, computedData: any) {
      const offeredPartnerTest = formData?.offer_partner_hiv_test === 'Yes'
      this.saveFormEncounterAndProceed({
        encounterTypeID: this.service.encounterTypeID,
        formData,
        computedData,
        skipNextTask: offeredPartnerTest,
        callback: () => {
          if (offeredPartnerTest) {
            this.$router.push('/patient/registration')
          }
        }
      })
    },
    partnerPresentField(): Field {
      return {
        id: 'partner_present',
        helpText: 'Partner',
        type: FieldType.TT_YES_NO,
        isRequired: () => true,
        finalComputedValue: (v: any) => {
          return {
            obs: this.service.buildValueText('Partner present', v)
          }
        },
        options: () => {
          return [
            {
              label: 'Is partner present at this session?',
              value: '',
              values: this.yesNoOptions()
            }
          ]
        }
      }
    },
    partnerHivStatusField(): Field {
      return {
        id: 'partner_hiv_status',
        helpText: 'Partner HIV Status',
        type: FieldType.TT_SELECT,
        isRequired: () => true,
        finalComputedValue: (v: Option) => {
          return {
            obs: this.service.buildValueCoded('Partner HIV Status', v.value)
          }
        },
        options: (f: any) => {
          const options = [
            this.toOption('HIV unknown'),
            { 
              label: 'HIV Negative',
              value: 'Negative'
            },
            this.toOption('Positive ART unknown'),
            this.toOption('Positive on ART'),
            this.toOption('Positive NOT on ART'),
          ]
          if (f.partner_present === 'No') {
            options.push(this.toOption('No partner'))
          }
          return options
        }
      }
    }
  }
})
